/*
  Theme and global variables
*/
html {
  overflow-y: scroll;

  --game-spacing: 32px;
  --header-height: 4rem;

  --color-success: hsl(150deg 70% 30%);
  --color-warning: hsl(50deg 100% 30%);
  --color-error: hsl(0deg 70% 45%);
  --color-gray-100: hsl(0deg 0% 10%);
  --color-gray-300: hsl(0deg 0% 25%);
  --color-gray-500: hsl(0deg 0% 50%);
  --color-gray-700: hsl(0deg 0% 75%);
  --color-gray-900: hsl(0deg 0% 90%);
}

/*
  Custom styles
*/
body {
  font-family: sans-serif;
}

@media (max-height: 600px) {
  body {
    --game-spacing: 8px;
  }
}

button {
  margin: 0;
  padding: 0;
  border: none;
  background: transparent;
  cursor: pointer;
  text-align: left;
  color: inherit;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

header {
  display: flex;
  height: var(--header-height);
  border-bottom: 1px solid var(--color-gray-700);
  color: var(--color-gray-300);
}

header .side {
  width: var(--header-height);
  display: grid;
  place-content: center;
}

h1 {
  flex: 1;
  font-size: 2rem;
  line-height: var(--header-height);
  text-align: center;
}

@media (max-width: 25rem) {
  h1 {
    font-size: 1.25rem;
  }
}

.game-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: var(--game-spacing);
  padding: var(--game-spacing) 10px;
  margin: 0 auto;
  min-width: 300px;
  max-width: min(500px, 58vh, 100%);
}

.guess-results,
.keyboard {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.guess,
.keyboard-row {
  display: flex;
  gap: 4px;
  margin-bottom: 4px;
}

.cell {
  position: relative;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1 / 1;
  border: 2px solid var(--color-gray-700);
  border-radius: var(--radius);
  font-size: 2rem;
}

.keyboard {
  margin-top: var(--game-spacing);
}

.keyboard-row {
  justify-content: center;
}

.keyboard-row:nth-of-type(2) {
  padding-left: 18px;
  padding-right: 18px;
}

.keyboard-row:nth-of-type(3) {
  padding-left: 54px;
  padding-right: 54px;
}

.keyboard-key {
  width: 2rem;
  aspect-ratio: 1 / 1;
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  background-color: var(--color-gray-900);
  border-radius: 4px;
  outline-offset: 2px;
  transition: background-color 0.5s;
}

.keyboard-key:hover {
  background-color: var(--color-gray-700);
  transition-duration: 0.1s;
}

.keyboard-key:active {
  background-color: var(--color-gray-500);
}

/*
  Round the corners for the 4 cells at the very corners of
  the game board
*/
.guess:first-of-type .cell:first-of-type {
  --radius: 4px 0px 0px 0px;
}
.guess:first-of-type .cell:last-of-type {
  --radius: 0px 4px 0px 0px;
}
.guess:last-of-type .cell:last-of-type {
  --radius: 0px 0px 4px 0px;
}
.guess:last-of-type .cell:first-of-type {
  --radius: 0px 0px 0px 4px;
}

.cell.correct,
.keyboard-key.correct {
  background: var(--color-success);
  border-color: var(--color-success);
  color: white;
}
.cell.incorrect,
.keyboard-key.incorrect {
  background: var(--color-gray-300);
  border-color: var(--color-gray-300);
  color: white;
}
.cell.misplaced,
.keyboard-key.misplaced {
  background: var(--color-warning);
  border-color: var(--color-warning);
  color: white;
}

.guess-input-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
  height: 6.75rem;
}

.guess-input-wrapper label {
  font-size: 1.25rem;
}

.guess-input-wrapper input {
  display: block;
  width: 100%;
  font-size: 2rem;
  border: 2px solid var(--color-gray-300);
  border-radius: 4px;
  padding: 8px 16px;
  outline-offset: 4px;
}

.guess-input-cover {
  position: relative;
}

.guess-submit-button {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  aspect-ratio: 1 / 1;
  margin: auto 0;
  text-align: center;
  line-height: 3.1;
  font-size: 1.5rem;
  font-weight: 600;
  transition: color 0.2s;
  outline-offset: -4px;
}

.guess-submit-button:hover {
  color: var(--color-gray-300);
}

.banner {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 32px;
  text-align: center;
  animation: slideUp 750ms cubic-bezier(0, 0.72, 0.24, 1.02);
  border-radius: 4px 4px 0px 0px;
  will-change: transform;
}

.happy.banner {
  background: var(--color-success);
  color: white;
}
.sad.banner {
  background: var(--color-error);
  color: white;
}

.reset-button {
  margin-top: 12px;
  padding: 6px 12px;
  font-weight: 600;
  color: var(--color-gray-100);
  background-color: var(--color-gray-900);
  border-radius: 4px;
  transition: background-color 0.2s;
  outline-offset: 2px;
}

.reset-button:hover {
  background-color: var(--color-gray-700);
}

.reset-button:active {
  background-color: var(--color-gray-500);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

/*
  Modal styles

  HACK: I'm doing this in a quick and dirty way.
  Later in the course, we'll see my preferred way of building
  modals in React.
*/
[data-reach-dialog-content] {
  position: relative;
  border-radius: 8px;
  padding: 24px 32px;
}
.modal-close-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: 16px;
  cursor: pointer;
}
.modal-title {
  margin-bottom: 0.5em;
}

/*
  Keyframe animations
*/
@keyframes slideUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0%);
  }
}
